/* Hamburger menu styles */
.hamburger {
  cursor: pointer;
  font-size: 30px;
  color: white;
  transition: transform 0.3s ease-in-out;
}

.hamburger.open {
  transform: rotate(90deg); /* Rotate the hamburger 90 degrees when the menu is open */
}

.hamburger:hover {
  transform: scale(1.2); /* Adds a hover effect to the hamburger */
}

/* Dropdown menu styles */
.dropdown-menu {
  position: absolute;
  top: 60px; /* Adjust this based on your layout */
  left: 0;
  width: 200px; /* Adjust width of dropdown */
  background-color: #2d2d2d; /* Dark background for the dropdown */
  padding: 16px;
  border-radius: 8px;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-100%); /* Initially off-screen */
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

/* When menu is open, make it visible and slide in */
.dropdown-menu.open {
  opacity: 1;
  visibility: visible;
  transform: translateX(0); /* Move the menu into view from left */
}

/* Input field styling inside the dropdown */
.dropdown-input {
  width: 100%;
  padding: 8px;
  margin-top: 8px;
  border: 1px solid #fff;
  border-radius: 4px;
  background-color: #333;
  color: white;
  outline: none;
}

.dropdown-input:focus {
  border-color: #4CAF50; /* Change border color on focus */
  background-color: #444;
}

/* Optional: Add styles for the rest of your layout */
.bg-black {
  background-color: #000;
